.createTask-header
    display: flex
    align-items: center
    justify-content: space-between
    padding: 0 20px
    width: 500px
    .createTask-buttons
        display: flex
        gap: 5px
        .createTask-create
            background-color: #55ef88
        .createTask-cancel
            background-color: #f39274

.createTask-input
    height: 30px
    outline: none
    border-radius: 5px
